* {
  padding: 0;
  margin: 0;
}

#root,
body {
  padding: 0;
  margin: 0;
}

.navbar-brand {
  font-size: 25px !important;
  font-style: italic;
  color: white !important;
}

.navbar {
  background-color: black !important;
  color: white !important;
}

.navbar .navbar-nav a {
  color: wheat !important;
  font-size: 18px !important;
  transition: 0.2s linear;
}

.navbar .navbar-nav a:hover {
  color: green !important;
}

.main-page {
  background: url("/static/images/house.jpg") no-repeat 50% 50% !important;
  background-size: cover !important;
  object-fit: cover !important;
}

.row {
  padding: 0 !important;
  margin: 0 !important;
  display: flex !important;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.content {
  display: flex !important;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  color: white;
  background-color: black;
}

.content h5 {
  font-size: 30px;
  font-family: Impact, Haettenschweiler, "Arial Narrow Bold", sans-serif;
}

.section-2 {
  display: flex;
  padding: 5px;
  flex-wrap: wrap;
  background: radial-gradient(
    circle,
    rgba(34, 193, 195, 1) 0%,
    rgba(253, 187, 45, 1) 80%
  );
}

.section-2 .box-content {
  display: flex;
  flex-basis: 30%;
  justify-content: center;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  text-align: left;
}

.section-2 .box-content span {
  font-size: 40px;
}

.section-2 .box-content:nth-child(2) span {
  color: red !important;
}

.section-2 .box-content:nth-child(3) span {
  color: white;
}

.section-2 .box-content p {
  line-height: 20px;
  letter-spacing: 1px;
  text-align: left;
  padding: 5px 10px;
  font-size: 18px;
}

.link_tab {
  font-size: 20px;
  color: green;
}
.navbar-toggler {
  background-color: white !important;
  order: -1 !important;
}
.navbar-toggler-icon {
  color: white !important;
}

.spinner {
  background: url("/static/images/spinner.gif") no-repeat 50% 50%
    rgba(0, 0, 0, 0.5);
  background-size: 15%;
  top: 0;
  left: 0;
  z-index: 999;
  height: 100%;
  width: 100%;
  position: fixed;
}

.dropdown-menu {
  overflow: auto !important;
  background-color: brown !important;
}

.disabled {
  color: white !important ;
}
/* .col {
  padding: 0 !important;
  margin: 0 !important;
} */

.card_content {
  margin-top: 30px;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}
.card_content img {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.card_content .card_text {
  margin-top: 30px;
  margin-left: 10px;
  margin-right: auto;
  padding: auto 10px !important;
}

.card_content .card_text h4 {
  font-weight: 900;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

.nav {
  width: 50% !important;
  margin-left: auto;
  margin-right: auto;
  margin-top: 30px;
  z-index: 1000;
}

.nav-item i {
  margin-right: 10px;
}

/* .nav .nav-item {
  padding: 10px 15px !important;
  background-color: orange;
} */

/* .nav .nav-item .nav-link {
  color: white;
  transition: 0.2s linear;
} */
.bord {
  border-width: 0;
  transition: 0.2s linear;
}

.nav .nav-item .nav-link:hover {
  color: wheat !important;
  font-weight: bolder;
}

.nav-link:hover.bord {
  opacity: 1 !important;
  border-bottom: 2px solid green;
}

.search-box {
  position: relative !important;
}

.search-box .search-result {
  position: absolute !important;
  top: 100%;
  background-color: grey;
  width: 100%;
  height: 50vh;
  display: flex;
  flex-direction: column;
  padding: 5px;
  z-index: 1000;
  transition: all 0.2s linear;
}
.search-post {
  display: flex;
  font-size: 12px;
  justify-content: space-between;
  flex-direction: row;
}

.search-post h3 {
  font-size: 10px;
}

.imgBox {
  padding: 10px;
}
.imgBox img {
  width: 100%;
  height: 100vh;
  object-fit: cover;
}

.post-detail {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  text-align: justify;
}
.post-detail h4 {
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  font-weight: 900;
  font-style: oblique;
}

.go-right {
  display: flex !important;
  justify-content: flex-end;
  color: grey;
  float: right !important;
}

.post-title {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.related-posts {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
}

.related-posts h4 {
  text-align: center;
}

.password-change {
  display: flex;
  flex-direction: column;
  margin-top: 30px;
  width: 70%;
}
.bold-text {
  font-weight: 300;
}
.password-change h5 {
  font-weight: bold;
  text-align: center;
}

.reset-password {
  margin-top: 60px;
  display: flex;
  flex-direction: column;
}

.reset-password h4 {
  text-align: center;
}

@media only screen and (max-width: 1200px) {
  .content {
    margin-top: 50px;
  }
}

@media only screen and (max-width: 950px) {
  .section-2 .box-content {
    flex-basis: 100% !important;
    text-align: center;
  }
  .section-2 .box-content p {
    text-align: center;
  }
  .row {
    flex-direction: column;
  }
  .col-12 img {
    width: 100%;
  }
  .content {
    margin-top: 30px;
  }
}
